<template>
  <el-aside width="200px">
    <ul class="side-nav">
      <li v-for="(item,index) of data" :key="index">
        <a
          :class="{active:currComp==item.comp}"
          href="javascript:void(0)"
          @click="urlTo(item)"
        >{{item.name}}</a>
        <!-- <router-link :to="{path:type,query:{comp:item.comp}}">{{item.name}}</router-link> -->
        <!-- <router-link :to="`/${type}/${item.comp}`">{{item.name}}</router-link> -->
      </li>
    </ul>
  </el-aside>
</template>
<script>
export default {
  name: "side-nav",
  props: {
    data: Array,
    type: String
  },
  data() {
    return {
      currComp: ""
    };
  },
  watch: {},
  created() {
    this.currComp = this.$route.query.comp || "log";
  },
  methods: {
    urlTo(row) {
      if (this.currComp == row.comp) {
        return;
      }
      this.currComp = row.comp;
      this.$router.push({ path: this.type, query: { comp: row.comp } });
    }
  }
};
</script>

